// import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// import additional modules
import {
  Navigation,
  Pagination,
  Scrollbar,
  FreeMode,
  EffectCube,
  EffectFlip,
  EffectCoverflow,
  EffectCards,
  Autoplay,
} from "swiper";

// import Swiper styles
import "swiper/css";
import "swiper/css/bundle";

// import images
import img1 from "./../img/img (1).jpg";
import img2 from "./../img/img (1).png";
import img3 from "./../img/img (2).jpg";
import img4 from "./../img/img (2).png";
import img5 from "./../img/img (3).jpg";
import img6 from "./../img/img (3).png";
import img7 from "./../img/img (4).jpg";
import img8 from "./../img/img (5).jpg";

function SwiperCarousel(props) {
  return (
    <Swiper
      // install swiper modules
      modules={[Navigation, Pagination, Scrollbar, FreeMode, EffectCube]}
      direction={"horizontal"}
      spaceBetween={30}
      navigation={true}
      pagination={{ clickable: true, dynamicBullets: true }}
      freeMode={true}
      effect={"cube"}
      cubeEffect={{
        shadow: true,
        slideShadows: true,
        shadowOffset: 20,
        shadowScale: 0.94,
      }}
      speed={600}
      grabCursor={true}
      centeredSlides={false}
      autoplay={{
        delay: 0,
        disableOnInteraction: true,
      }}
      slidesPerView={1}
      onSlideChange={() => console.log("slide change")}
      onSwiper={(swiper) => console.log(swiper)}
      loop={true}
    >
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img1} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img2} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img3} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img4} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img5} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img6} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img7} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
      <SwiperSlide>
        <div className="flex h-60 w-full">
          <img src={img8} alt="anime" className="w-full object-cover" />
        </div>
      </SwiperSlide>
    </Swiper>
  );
}

export default SwiperCarousel;
