import React from "react";
import Slider from "react-slick"; // an component
import img1 from "./../img/img (1).jpg";
import img2 from "./../img/img (1).png";
import img3 from "./../img/img (2).jpg";
import img4 from "./../img/img (2).png";
import img5 from "./../img/img (3).jpg";
import img6 from "./../img/img (3).png";
import img7 from "./../img/img (4).jpg";
import img8 from "./../img/img (5).jpg";
import nextIcon from "./../img/next-icon.png";

const SlickArrowLeft = ({ currentSlide, slideCount, ...props }) => (
  <button
    {...props}
    className={
      "slick-prev slick-arrow" + (currentSlide === 0 ? " slick-disabled" : "")
    }
    aria-disabled={currentSlide === 0 ? true : false}
    type="button"
  >
    <div className="flex h-16 w-16 -translate-y-[38%] -translate-x-[68%]">
      <img src={nextIcon} alt="" className="rotate-180 opacity-70" />
    </div>
  </button>
);

const SlickArrowRight = ({ currentSlide, slideCount, ...props }) => (
  <button
    {...props}
    className={
      "slick-next slick-arrow" +
      (currentSlide === slideCount - 1 ? " slick-disabled" : "")
    }
    aria-disabled={currentSlide === slideCount - 1 ? true : false}
    type="button"
  >
    <div className="flex h-16 w-16 -translate-y-[38%]">
      <img src={nextIcon} alt="" className="opacity-70" />
    </div>
  </button>
);

export default function SimpleSlider() {
  let settings = {
    dots: true,
    arrows: true,
    infinite: false,
    speed: 500,
    centerMode: false,
    accessibility: true,
    slidesToShow: 3,
    slidesToScroll: 3,
    adaptiveHeight: true,
    nextArrow: <SlickArrowRight />,
    prevArrow: <SlickArrowLeft />,
    // nextArrow: (
    //   <button>
    //     {/* before : none */}
    // <div className="flex h-16 w-16 -translate-y-[38%]">
    //   <img src={nextIcon} alt="" className="opacity-70" />
    // </div>
    //   </button>
    // ),
    // prevArrow: (
    //   <button>
    //     {/* before : none */}
    // <div className="flex h-16 w-16 -translate-y-[38%] -translate-x-[68%]">
    //   <img src={nextIcon} alt="" className="rotate-180 opacity-70" />
    // </div>
    //   </button>
    // ),
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
          dots: true,
          arrows: false,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: true,
          arrows: false,
        },
      },
    ],
  };
  return (
    <>
      <h1 className="my-5 text-center text-3xl font-medium">
        React Slick Carousel
      </h1>
      <Slider {...settings} className="h-56 w-full">
        {/* react slick will add inline style to this slick-list : display: inline-block, and width: 100%*/}
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img1} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img2} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img3} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img4} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img5} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img6} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img7} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
        <div className="h-56">
          <div className="h-full sm:px-2">
            <div className="flex h-full w-full justify-items-center">
              <img src={img8} alt="anime" className="w-full object-cover" />
            </div>
          </div>
        </div>
      </Slider>
    </>
  );
}
