import React, { Component } from "react";
import anime from "animejs";

export default class Anime extends Component {
  componentDidMount() {
    anime({
      targets: ".animate-this",
      translateX: 250,
    });
  }
  render() {
    return <div className="animate-this m-3 h-10 w-10 bg-slate-500"></div>;
  }
}
